:root {
  --bg-color-mobile: #010409;
  --bg-color-900: #0d1117;
  --bg-header: #161b22;
  --bg-color-p: #161d27;
  --skin-color: #a13e3e;
  --text-300: #c9d1d9;
  --text-400: #8b949e;
}


body {
  margin: 0;
  font-family: 'roboto mono', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 16px;
}

p{
  color: #8b949e;
  color: var(--text-400);
  line-height: 1.6;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@-webkit-keyframes blinker {
    50% {
      opacity: 0;
    }
}

@keyframes blinker {
    50% {
      opacity: 0;
    }
}

.header{
    display: flex;
    justify-content: space-between;
    flex: 1 1;
    padding: 15px 0px 15px 15px;
    position: relative;
    align-items: center;
    width: 100%;
}

.menu{
    display: flex;
    flex: 1 1;
    justify-content: flex-end;
}

.menu-logo{
    font-size: 20px;
    font-weight: 600;
    box-shadow: inset 0px -4px 0 0 var(--skin-color);
    margin-right: 50px;
}

.mobile-menu{
    display: none;
}

.web-menu{
    display: flex;
}
.logo{
    display: flex;
    font-size: 29px;
    font-weight: 900;
}
.logo a{
    text-decoration: none;
    color: var(--text-300)
}
.logo a:hover::after {
    content: "|";
    position:relative;
    bottom: 3px;
    left: -2px;
    -webkit-animation: blinker 1.5s linear infinite;
            animation: blinker 1.5s linear infinite;
    width: 100%;
}

.logo-point{
    font-size: 30px;
}
.logo-bracket{
    color: var(--skin-color);
    position: relative;
    bottom: 2px;
}

@media only screen and (max-width: 900px) {
    .mobile-menu{
        display: flex;
    }
    .header{
        width: 100%;
        padding: 15px 20px 15px 20px;
    }
    .web-menu{
        display: none;
    }
    .logo{
        font-size: 26px;
    }
}
@media only screen and (max-width: 512px) {
    .menu-logo{
        font-size: 18px;
        margin-right: 10px;
    }
    .logo{
        font-size: 22px;
    }
    .header{
        padding-top: 10px;
        padding-right: 10px;
        padding-left: 5px;
    }
}

.mobile{
  background-color: var(--bg-color-mobile);
  height: 100vh;
  width: 50vw;
  position: absolute;
  right: 0px;
  top: -17px;
  padding: 39px 12px;
  display: flex;
  flex-direction: column;
  z-index: 1;
}


.close{
    display: flex;
    justify-content: flex-end;
    margin-top: 40px;
    margin-right: 76px;
}

.close-menu {
    position: absolute;
    top: 40px;
    width: 32px;
    height: 32px;
    right: 80px;
}
.close-menu:before, .close-menu:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 33px;
  width: 2px;
  background-color: var(--skin-color);
}
.close-menu:before {
  -webkit-transform: rotate(45deg);
          transform: rotate(45deg);
}
.close-menu:after {
  -webkit-transform: rotate(-45deg);
          transform: rotate(-45deg);
}
  
.mobile-options{
  display: flex;
  flex-direction: column;
  width: 100%;
  padding-top: 80px;
  align-items: center;
  height: 100%;
}
.mobile-option{
  font-size: 20px;
  margin: 20px 0;
  display: flex;
}
.mobile-option a{
  text-decoration: none;
  color: var(--text-300);
}
.mobile-option a:hover{
  color: var(--skin-color);
}

@media only screen and (max-width: 512px) {
  .close{
    margin-right: 20px;
    margin-top: 30px;
  }
  .close-menu{
    right: auto;
  }
  .mobile{
    top: -23px;
  }

}
.web{
    display: flex;
    flex: 1 1;
    align-items: center;
}

.web-option {
    font-size: 16px;
    margin-right: 50px;
    font-weight: 600;
}
.web-option a{
    text-decoration: none;
    color: var(--text-300);
    display: flex;
    align-items: center;
}
.web-option a:hover{
    color: var(--skin-color);
}
.about {
  width: 100%;
  margin-bottom: 200px;
}

.about-heading h1 {
  display: flex;
  align-items: center;
  font-size: 18px;
  color: var(--skin-color);
}
.about-heading h2 {
  display: flex;
  align-items: center;
  font-size: 100px;
  margin: 0px 0;
}

.about-heading h2 + h2 {
  color: var(--text-400);
}

.about-bottom {
  margin-top: 25px;
  display: flex;
  justify-content: space-between;
}

.about-photo {
  margin-left: 80px;
  margin-right: 20px;
}
.about-img {
  height: 360px;
  border-radius: 5px;
  -webkit-filter: grayscale(0.4);
          filter: grayscale(0.4);
}


.about-img:hover {
  -webkit-filter: grayscale(0.2);
          filter: grayscale(0.2);
}

.about-info span {
  font-weight: bold;
  color: var(--skin-color);
}

@media only screen and (max-width: 2200px) {
  .about-heading h2 {
    font-size: 4.5vw;
  }
}

@media only screen and (max-width: 1000px) {
  .about-bottom {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .about-img {
    height: 320px;
  }
  .about-photo {
    margin-left: 0px;
    margin-right: 0px;
    margin-top: 15px;
  }
  .about-heading h2 {
    font-size: 50px;
  }
}
@media only screen and (max-width: 600px) {
  .about-heading h2 {
    font-size: 40px;
  }
}


.social-contact{
    display: flex;
}
.social-icons{
    margin-top: 15px;
}
.social-icon{
    height: 35px;
    color: var(--text-400);
}
.social-icon:hover{
    color: var(--skin-color);
}

#social-icon-margin{
    margin-left: 10px;
}
.projects{
    display: flex;
    flex-direction: column;
    flex: 1 1;
}

.section-title{
    font-size: 50px;
    font-weight: bold;
    margin-bottom: 50px;
}

@media only screen and (max-width: 1000px) {
    .section-title{
        font-size: 40px;
        margin-bottom: 20px;
    }
}

.span-section-title{
    color: var(--skin-color);
}
.project-card{
    flex: 1 1;
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 100px;
}


.project-picture{
    height: 320px;
    position: relative;
    right: 80px;
    z-index: -1;
    border-radius: 5px;
}

.project-title{
    font-size: 20px;
    text-decoration: none;
    color: var(--text-300);
}
.project-title:hover{
    color: var(--skin-color);
}


.about-project{
    background-color: var(--bg-color-p);
    padding: 10px;
    border-radius: 5px;
}

.tag{
    margin-right: 15px;
    color: var(--text-400);
    font-size: 13px;
}

.project-links{
    margin-top: 16px;
}

.project-link{
    font-size: 20px;
    margin-right: 15px;
    color: var(--text-300);
}

.project-link:hover{
    color: var(--skin-color);
}

@media only screen and (max-width: 1000px) {
    .project-card{
        flex-flow: column;
    }
    .project-picture{
        right: 0;
        height: 400px;
        order: -1;
        margin-top: 40px;
        margin-bottom: 40px;
    }
    .about-project{
        background-color: transparent;
        padding: 0;
    }
}

@media only screen and (max-width: 800px) {
    .project-picture{
        height: 320px;
    }

}

@media only screen and (max-width: 700px) {
    .project-picture{
        height: 250px;
    }
}

@media only screen and (max-width: 500px) {
    .project-picture{
        height: 200px;
    }
}

@media only screen and (max-width: 400px) {
    .project-picture{
        height: 150px;
    }
}
.contact{
    text-align: center;
}

.about-contact{
    width: 500px;
    position: relative;
    margin: auto;
    margin-top: 20px;
    margin-bottom: 50px;
}

.contact-email-link{
    font-size: 18px;
    font-weight: bold;
    color: var(--text-300);
    font-family: 'roboto mono', sans-serif;
    padding: 10px;
    background-color: transparent;
    border-radius: 5px;
    border: 1px solid var(--text-400);
    text-decoration: none;
}

.contact-email-link:hover{
    background-color: #a13e3e9d;
}

@media only screen and (max-width: 600px) {
    .about-contact{
        width: auto;
    }
}
.body {
  padding-top: 100px;
  padding-bottom: 100px;
  margin-left: 10%;
  margin-right: 10%;
}

@media only screen and (max-width: 1450px) {
  .body {
    margin-left: 5%;
    margin-right: 5%;
  }
}
@media only screen and (max-width: 800px) {
    .body{
        padding-top: 0px;
    }
  }
  
.about-footer{
    font-size: 13px;
    text-align: center;
    margin-top: 50px;
    margin-bottom: 30px;
}
html {
    scroll-behavior: smooth;            
}
body{
    background-color: var(--bg-color-900);
    color: var(--text-300);
    overflow-x: hidden;
}
.home{
    display: flex;
    flex-direction: column;
    margin: auto;
    width: 1340px;
}

/* Scrollbar Styling */
::-webkit-scrollbar {
    width: 5px;
}
 
::-webkit-scrollbar-track {
    background-color: var(--bg-color-900);
    border-radius: 10px;
}

::-webkit-scrollbar-thumb {
    border-radius: 10px;
    background: var(--text-400); 
}
@media only screen and (max-width: 1340px) {
    .home{
        width: 100%;
    }
}
